.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 0 auto;
  max-width: 500px;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

p {
  font-size: 14px;
  text-align: center;
  margin-bottom: 15px;
}

.fileInputLabel {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
}

.fileInput {
  display: none;
}

.uploadButton {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 16px;
}

.uploadButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.fileDetails {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.fileDetails h3 {
  font-size: 14px;
  margin-bottom: 5px;
}

.successMessage {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #a4d185; /* Green border for success */
  border-radius: 5px;
  background-color: #f5f9f2; /* Light green background */
  color: #388e3c; /* Dark green text */
}

.successMessage h3 {
  font-size: 16px;
  margin-bottom: 5px;
}

.successMessage p {
  line-height: 1.5;
}